/* модальне выкно */
.cartmodal {
        position: fixed;
        top: 0;
        right: 0;
        height: 100%;
        background-color: #f8f9fa;
        padding: 20px;
        overflow-y: auto;
        box-shadow: -2px 0 5px rgba(0, 0, 0, 0.1);
        background-color: white;
        padding: 20px;
        border-radius: 10px;
        width:80%;
        box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Кошик титул */
.carttitle {
    font-size: 24px;
    font-weight: bold;
    margin-top: 50px;
    text-align: center;
}
/* Кнопка закриття модального выкна */
.cartclosebtn {
    background-color: crimson;
        /* Цвет кнопки закрытия бургера */
        color: #ffffff;
        border: 1px solid;
        border-color: #d1d1d1;
        border-radius: 100%;
        padding: 1px;
        height: 50px;
        width: 50px;
        margin-left: -45px;
        top: 0;
        position: fixed;
}
/* Список товарыв */
.cartlist {
    list-style-type: none;
    padding: 0;
}
/* Товар */
.cartitem {
    margin-bottom: 20px;
    align-items: start;
    margin-top: 60px;
    border-bottom:  1px solid; 
}
/* Картинка */
.cartimg {
    width: 30%;
    height: auto;
    object-fit: cover;
    border: 1px solid burlywood;
    float: left;
}
/* Назва товару */
.cartitemtitle {
    font-size: 16px;
    line-height: 1.2;
    padding-left: 15px;
    float: right;
    
}
/* Кнопка видалення товару */
.itemclosebtn {
    background: none;
    border: none;
    font-size: 20px;
    display: inline-block;
    top: 0;
}

.countercontainer {
    display: flex;
    padding-left: 110px;
    margin-bottom: 10px;
}

.countmin{
    border: 1px solid;
    background: transparent;
    font-size: 20px;
    height: 16px;
    line-height: 5px;
    padding: 3;
}
.countplus {
    border:1px solid;
    background: transparent;
    font-size: 18px;
    height: 16px;
    line-height: 12px;
    padding: 3;
   
    
}

.cartitemcount {
    font-size: 18px;
    border: 1px solid;
    text-align: center;
    line-height: 28px;
    height: 30px;
    width: 35px;
    padding: 0;
    font-size: 90%;
}

.cartprice {
    font-size: 18px;
    font-weight: bold;
    align-items: center;
    margin-left: 100px;
}

.checkoutbtn {
    background-color: #3498DB;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    font-size: 18px;
    cursor: pointer;
    
}

.checkoutbtn:hover {
    background-color: #2980B9;
}
.cartproduct{
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
}
.countgrid{
    display: grid;
}
.cost{
    display: flex;
    align-items: center;
    justify-content: space-between;
}