.contentContainer{
    margin-top: 110px;
}
.closePageBtn{
    position: fixed;
    margin-left: 300px;
    bottom: 0;
}

.productitem {

    border: 1px solid;
    border-color: rgb(251, 249, 248);
    margin: 8px;
    margin: 8px;
    height: auto;
    width: auto;
    margin-bottom: 15px;
}

.modal {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    display: flex;
    justify-content: center;
    align-items: center;
}

.modalcontent {
    background: #fff;
    padding: 20px;
    border-radius: 5px;
}
.productlist {

    display: grid;
    grid-template-columns: repeat(2, 200px);
    justify-content: space-between;
    grid-row-gap: 5px;
    grid-column-gap: 5px;
    margin-top: 140px;
    margin-bottom: 80px;

}